import SimpleLightbox from 'simplelightbox';
import 'simplelightbox/dist/simple-lightbox.min.css';
import ApiService from './api-service';
import { Notify } from 'notiflix/build/notiflix-notify-aio';
import { Loading } from 'notiflix/build/notiflix-loading-aio';
import scrollMonitor from 'scrollmonitor';

//*   Notiflix    
Notify.init({
  position: 'right-bottom',
});

//*    Loading   
Loading.init({
  backgroundColor: 'rgba(0,0,0,0.3)',
  svgColor: 'rgb(60, 197, 218)',
  clickToClose: false,
});

//*     DOM ( )      
const form$ = document.getElementById('search-form'); //*  
const imageContainer$ = document.querySelector('.gallery'); //*   

//*          
const search = new ApiService();
const lightBox = new SimpleLightbox('.gallery a');

//*        
const scrollListener = scrollMonitor.create(imageContainer$);
scrollListener.partiallyExitViewport(loadMore);

//* C
form$.addEventListener('submit', submitForm);

//* -,      
function submitForm(event) {
  event.preventDefault(); //*  c  
  imageContainer$.innerHTML = ''; //*    
  Loading.dots(); //*   
  search.searchQuery = form$.elements.searchQuery.value; //*     
  search.resetPage(); //*    
  addImageAndUpdateUI(); //*  -      
}

//* -     
function renderImage(array) {
  if (!array.length) {
    Notify.failure(
      'Sorry, there are no images matching your search query. Please try again.'
    );
    Loading.remove(); //*   
    return;
  }
  const markup = array
    .map(
      ({
        webformatURL,
        largeImageURL,
        tags,
        likes,
        views,
        comments,
        downloads,
      }) => {
        return `<div class="photo-card">
        <a href="${largeImageURL}">
            <img src="${webformatURL}" alt="${tags}" loading="lazy" />
            <div class="info">
              <p class="info-item">
                <b>Likes</b>
                ${likes}
              </p>
              <p class="info-item">
                <b>Views</b>
                ${views}
              </p>
              <p class="info-item">
                <b>Comments</b>
                ${comments}
              </p>
              <p class="info-item">
                <b>Downloads</b>
                ${downloads}
              </p>
            </div></a>
          </div>`;
      }
    )
    .join('');
  imageContainer$.insertAdjacentHTML('beforeend', markup); //*  HTML    
  lightBox.refresh(); //*   

  Loading.remove(); //*   

  search.addPage(); //*     
}
